<template>    <n-card @click="toSongList(id)" footer-style="padding: 5px 10px;">        <template #cover>            <img :src="imgSrc" alt="">        </template>        <template #footer>            <n-ellipsis>                {{title}}            </n-ellipsis>        </template>    </n-card></template><script>import {NCard, NEllipsis} from 'naive-ui'import {useRouter} from 'vue-router'export default {    name: 'RecommendList',    components:{        NCard,        NEllipsis    },    props: ['id', 'title', 'imgSrc'],    setup(){        let router = useRouter()        function toSongList(id){            router.push('/songList/' + id)        }        return {            toSongList        }    }}</script><style scoped>.n-card{    max-width: 260px;    cursor: pointer;}</style>